import { Component, OnInit } from '@angular/core';
import { AdministerManageBean } from './bean/adnimister-manage.bean';
import { CommonHttpService } from '../../http/common.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
    selector: 'administer-manage',
    templateUrl: 'administer-manage.html',
    styleUrls: ['administer-manage.scss'],
    providers: [ConfirmationService,Commfunc]
})

/**
 * 机构对应关系
 */
export class AdministerManage implements OnInit {
    tabMesg: any = tableMessage;

    aaa: any;
    // 表头
    precredit: string = '管辖机构';

    //修改的参数
    updateValue: any = [];

    // 表体内容
    tableData: any;
    administerManageBean: AdministerManageBean = new AdministerManageBean()

    //总页数
    total: string;
    //每页多少条
    pageSize: string = '10';

    //提示信息
    msgs: Message[] = [];

    //模态框的标题
    headerTitle: string = "修改";
    showModel: any;

    //模态框是否显示
    display: boolean = false;

    //传入的值
    inValue: string;

    p_data: any;

    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc:Commfunc
    ) { }

    ngOnInit() {

        this.administerManageBean.pageSize = '10';
        this.administerManageBean.pageNumber = '1';
        this.queryClick();
    }

    //查询
    queryClick() {

    }


    // 重置
    reset() {
        this.administerManageBean = new AdministerManageBean()
    }


    //分页
    paginate(event) {
        //每页显示的条数
        this.administerManageBean.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1 + '';
        this.administerManageBean.pageNumber = currentPage;
        //调用查询的方法
        this.queryClick();
        this.administerManageBean.pageNumber = '1';
    }



    // 增加
    add() {
        this.headerTitle = '创建管辖机构';
        this.display = true;
        this.showModel = 1;

    }


    //添加后返回的值
    addCall(param) {
        this.display = param;
        this.administerManageBean.pageNumber = '1';
        this.queryClick();
    }


    // 修改
    update(item) {
        this.headerTitle = '管辖机构修改';
        this.display = true;
        this.showModel = 2;
        // this.updateValue = item.groupId;
    }


    //修改的回调
    updateCall(param) {
        this.display = param;
        this.administerManageBean.pageNumber = '1';
        this.queryClick();
    }

    //删除
    delete(car) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
 
            }
        });
    }


    //返回的值
    outValue(param) {
        this.display = param.display;
    }
    //按钮权限
  permissionCheck(id){
    return	this.commfunc.permissionCheck(id)
  }

}